@keyframes moveButton {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(3deg);
  }

  50% {
    transform: rotate(0deg);
  }

  75% {
    transform: rotate(-3deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

@keyframes activeLink {
  0% {
    width: 0;
  }

  100% {
    width: 80%;
  }
}

@keyframes activeLinkBack {
  0% {
    background-position: 100% 0, 100% 0;
  }

  100% {
    background-position: 0 100%, 0 100%;
  }
}
